'use client'

import { useRouter } from 'next/router'
import React from 'react'

function page() {

  const router = useRouter()

  const destinations = ['Paris', 'Tokyo', 'NewYork']

  return (
    <div className='flex flex-col justify-center items-center h-screen gap-4 text-white'>
      <div className='font-bold text-2xl'>Choose your Destination</div>
      <div className='flex flex-col gap-4'>
        {destinations.map((d, idx)=> (
          <div key={idx} className='text-black bg-white font-bold text-2xl flex items-center justify-center rounded-xl w-[200px] h-[100px] hover:opacity-[0.5] transition-all' onClick={()=> router.push("/[city]")}>{d}</div>
        ))}
      </div>
    </div>
  )
}

export default page
